<template>
  <div class="book" @mouseover="handleMouseOver" @mouseout="handleMouseOut">
    <div class="page">
      <div class="front cover">
        <h1>In Defense of Happily Ever-After</h1>
        <h2>Reevaluating the Romance Novel </h2>
        <h3>Sarah Bacharach</h3>
      </div>
      <div class="back">
        <p style="text-align: center; margin-top: 10vh; font-size: 30px; line-height: 1.5;">Prologue</p>
        <p style="text-align: left; font-size: 20px;line-height: 1.5;">Romance novels are often relegated to the back shelf, dismissed as fluffy escapes yet 1 in 3 books sold are romance. <br><br> How Can We Bridge the Gap Between Reader Perception and the Reality of Romance Novels?</p>
      </div> 
    </div>

    <div class="page">
      <div class="front">
        <p></p>
      </div>
      <div class="back">
        <p style="text-align: center; margin-top: 10vh; font-size: 30px; line-height: 1.5;">Chapter One <br> Beyond the Beach Read</p>
        <p style="text-align: left; font-size: 20px;line-height: 1.5;">Some say romance readers confine themselves to a single genre, consuming 4x the average reader, but within this exploring everything from fantasy to historical sagas. <br><br>These readers escape genre confinement by shear volume read. </p>
      </div>
    </div>

    <div class="page">
      <div class="front">
        <p style="font-size: 15px; font-family: 'EB Garamond', serif; color: #6B1F2B; text-align: center; margin-top: 25.5vh; margin-left: -1vw">
          <span style="display: block; font-size: 40px;">4x</span>
          <span style="display: block;">The Average Reader</span>
        </p>
        <div class="book-grid" v-if="showBookGrid">
          <div v-for="rowIndex in Math.ceil(52 / 6)" :key="rowIndex" class="book-row">
            <div v-for="colIndex in (rowIndex === Math.ceil(52 / 6) ? 4 : 6)" :key="(rowIndex - 1) * 6 + colIndex" class="book-column" :style="{ 'animation-delay': ((rowIndex - 1) * 6 + colIndex - (rowIndex === Math.ceil(52 / 6) ? 2 : 1)) * .1 + 's' }">
              <img src="@/assets/4x.jpg" alt="Book Icon" class="book-icon">
            </div>
          </div>
          <p v-if="showBooksPerYear" class="books-per-year" style="font-size: 15px;">= 52 books/year</p>
        </div>
      </div>
      <div class="back">
        <p style="text-align: center; margin-top: 110px; font-size: 40px; line-height: 1.5;"><br> </p>
        <p style="text-align: left; font-size: 25px;line-height: 1.5;"> </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      showBookGrid: false,
      showBooksPerYear: false 
    };
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll);
    const flipBook = (elBook) => {
      elBook.style.setProperty("--c", 2); 
      elBook.querySelectorAll(".page").forEach((page, idx) => {
        page.style.setProperty("--i", idx);
        page.addEventListener("click", (evt) => {
          if (evt.target.closest("a")) return;
          const curr = evt.target.closest(".back") ? idx : idx + 1;
          elBook.style.setProperty("--c", curr);
        });
      });
    };

    flipBook(this.$el);
  },
  destroyed() {
    window.removeEventListener('scroll', this.handleScroll);
  },
  methods: {
    handleScroll() {
      const triggerPoint = 790; 

      if (window.scrollY >= triggerPoint) {
        this.showBookGrid = true;
        setTimeout(() => {
          this.showBooksPerYear = true; 
        }, 5500);

        window.removeEventListener('scroll', this.handleScroll);
      }
    },
    handleMouseOver() {
      console.log('Mouse over book');
    },
    handleMouseOut() {
      console.log('Mouse out of book');
    }
  }
};
</script>

