<template>
  <div class="book">
    <div class="page">
      <div class="front cover">
        <h1>In Defense of Happily Ever-After</h1>
        <h2>Reevaluating the Romance Novel </h2>
        <h3>Sarah Bacharach</h3>
      </div>
      <div class="back">
        <p style="text-align: center; margin-top: 10vh; font-size: 30px; line-height: 1.5;">Prologue</p>
        <p style="text-align: left; font-size: 20px;line-height: 1.5;">Romance novels are often relegated to the back shelf, dismissed as fluffy escapes yet 1 in 3 books sold are romance. <br><br> How Can We Bridge the Gap Between Reader Perception and the Reality of Romance Novels?</p>
      </div> 
    </div>

    <div class="page">
        <div class="front">
            <svg id="my_dataviz" width="400" height="500" style="margin: auto;"></svg>
            <p style="text-align: center; font-size: 20px; position: absolute; top: 15vh; left: 0; right: 0;">Total Book Sales</p>
        </div>
        <div class="back"></div>
    </div>
</div>
</template>

<script>
import * as d3 from 'd3';
export default {
  mounted() {
    const flipBook = (elBook) => {
      elBook.style.setProperty("--c", 0); 
      elBook.querySelectorAll(".page").forEach((page, idx) => {
        page.style.setProperty("--i", idx);
        page.addEventListener("click", (evt) => {
          if (evt.target.closest("a")) return;
          const curr = evt.target.closest(".back") ? idx : idx + 1;
          elBook.style.setProperty("--c", curr);
        });
      });
    };

    flipBook(this.$el); 

    const data = [
      { Genre: "Romance", Revenue: 1440 },
      { Genre: "Mystery", Revenue: 728.2 },
      { Genre: "Religious", Revenue: 720 },
      { Genre: "Fantasy", Revenue: 590.2 },
      { Genre: "Horror", Revenue: 79.6 }
    ];

    const margin = { top: 140, right: 0, bottom: 30, left:200 };
    const width = 300;
    const height = 300;

const svg = d3.select("#my_dataviz")
  .append("svg")
  .attr("width", width + margin.left + margin.right)
  .attr("height", height + margin.top + margin.bottom)
  .append("g")
  .attr("transform", "translate(" + (width / 2) + "," + margin.top + ")")
  .attr("transform", "translate(" + (margin.left - 150) + "," + margin.top + ")")

  .attr("id", "my_svg");

    const x = d3.scaleBand()
      .range([0, width])
      .domain(data.map(d => d.Genre))
      .padding(0.2);

    const y = d3.scaleLinear()
      .domain([0, d3.max(data, d => d.Revenue)])
      .range([height, 0]);
    
  
svg.append("g")
  .attr("transform", "translate(0," + height + ")")
  .call(d3.axisBottom(x))
  .selectAll("text")
    .attr("transform", "translate(15,0)rotate(0)")
    .style("text-anchor", "end")
    .style("font-weight", function() {
      return this.textContent === "Romance" ? "bold" : "normal";
      })
    .attr("fill", function() {
      return this.textContent === "Romance" ? "#6B1F2B" : "#9e9b9d";
      })
  

    svg.append("g")
      .call(d3.axisLeft(y))  
      
      
    svg.selectAll("mybar")
      .data(data)
      .enter()
      .append("rect")
      .attr("x", d => x(d.Genre))
      .attr("width", x.bandwidth())
      .attr("fill", d => d.Genre === "Romance" ? "#6B1F2B" : "#9e9b9d")
      .attr("height", function() { return height - y(0); }) 
      .attr("y", function() { return y(0); }); 
    
     svg.selectAll("rect")
      .transition()
      .duration(15000)
      .attr("y", function(d) { return y(d.Revenue); })
      .attr("height", function(d) { return height - y(d.Revenue); })
      .delay(function(d, i) { return i * 0; });

    svg.append("text")
      .attr("transform", "translate(" + (width / 2.1) + " ," + (height + margin.top + 20) + ")")
      .style("text-anchor", "middle")
      .style("font-size", "14px")
      .text("Genre");

    svg.append("text")
      .attr("transform", "rotate(-90)")
      .attr("y", 158 - margin.left)
      .attr("x", 0 - (height / 2))
      .attr("dy", ".1em")
      .style("text-anchor", "middle")
      .style("font-size", "10px")
      .text("Revenue (millions)");  

      }
};
</script>

