<template>
  <div class="page-container" :style="{ backgroundColor: backgroundColor }">
    <div class="chapter-container" :class="{ 'hidden': scrolledPastChapterTriggerPoint }">
      <div class="chapter">{{ event.chapter }}</div>
      <div class="vertical-line"></div>
    </div>
    <div class="text-container">
      <div class="text-content">
        <h3> What are the needs of contemporary romance readers? This requires going beyond cover art analysis. Digging deeper, natural language processing examined the emotional landscape of nearly 1,500 romance novels and revealed <span style="background-color: #E6E279">78.3% offered positive sentiments</span>. Romance novels provide an important space for uplifting narratives amidst a world increasingly characterized by uncertainty.
        </h3>
      </div>
      <input type="range" min="0" max="100" v-model="sliderValue" @input="updateSliderValue">
    </div>
    <div class="sentiment">
      <div class="sentence-container">
        <div class="sentence">{{ currentSentence.title }}</div>
        <div class="description">"{{ currentSentence.description }}"</div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'SentimentAnalysis',
  data() {
    return {
      backgroundColor: '#C1B6D8',
      scrolledPastChapterTriggerPoint: false,
      event: { chapter: 'Ch. 3: Love Wins (Every Time)' },
      sentences: [],
      currentIndex: 0,
      sliderValue: 50 
    };
  },
  computed: {
    currentSentence() {
      return this.sentences[this.currentIndex];
    }
  },
  mounted() {
    this.loadData();
    window.addEventListener('scroll', this.handleScroll);
    this.handleScroll();
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.handleScroll);
  },
  methods: {
    handleScroll() {
      const scrollTop = window.scrollY;
      const chapterTriggerPoint = 1000;

      if (scrollTop >= chapterTriggerPoint) {
        this.scrolledPastChapterTriggerPoint = false;
      }
    },
    async loadData() {
      try {
        const response = await axios.get('/Data/sentiment_data.json'); 
        this.sentences = response.data.positive; 

        // Once the data is loaded, start the slider animation
        this.animateSlider();
      } catch (error) {
        console.error('Error loading data:', error);
      }
    },
    updateSliderValue() {
      const newIndex = Math.floor((this.sliderValue / 100) * (this.sentences.length - 1));
      this.currentIndex = newIndex;
    },
    animateSlider() {
      const animationDuration = 800000; 
      const totalSteps = this.sentences.length - 1;
      const stepDuration = animationDuration / totalSteps;
      let currentStep = 0;

      // Recursive function to update the slider value and current index
      const animateStep = () => {
        // Update the slider value and current index
        this.sliderValue = (currentStep / totalSteps) * 100;
        this.currentIndex = currentStep;

        // Increment the current step
        currentStep++;

        // Check if all steps are completed
        if (currentStep <= totalSteps) {
          // Schedule the next step after the specified step duration
          setTimeout(animateStep, stepDuration);
        }
      };

      // Start the animation
      animateStep();
    }
  }
};
</script>

