<template>
  <div>
    <div class="page-container" :style="{ backgroundColor: backgroundColor }">
      <div class="chapter-container">
        <div class="chapter">{{ event.chapter }}</div>
        <div class="vertical-line"></div>
      </div>
      <transition name="fade">
        <img v-if="showImage" src="@/assets/Quotes.svg" alt="Quotes" class="quotes-image">
      </transition>
      <div class="scroll-indicator-container">
        <Scrollama
          :offset=".5"
          @step-enter="handleStepEnter"
          @step-exit="handleStepExit"
        >
          <div v-for="n in 1" :key="n" :data-step-no="n" :class="{ 'step': true, 'active': n == currStep }">
          This genre is a powerhouse for female creativity, offering a world where women are not just the readers, but the writers, the publishers, the CEOs, designers, and managers. 
          </div>
        </Scrollama>
      </div>
    </div>
  </div>
</template>

<script>
import Scrollama from "vue-scrollama";

export default {
  name: 'QuotePage',
  components: {
    Scrollama,
  },
  data() {
    return {
      backgroundColor: '#C0D9DC',
      event: { chapter: 'Ch. 4: Celebrating the Female Gaze' },
      showImage: false,
      currStep: null,
      scrollamaInitialized: false
    };
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll);
    this.handleScroll(); 
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.handleScroll);
  },
  methods: {
    handleScroll() {
      const scrollTop = window.scrollY;
      const fadeTriggerPoint = 9300; 

      if (scrollTop >= fadeTriggerPoint) {
        this.showImage = true;
        this.scrollamaInitialized = true; 
      } else {
        this.showImage = false;
      }
    },
    handleStepEnter({ element }) {
      this.currStep = element.dataset.stepNo;
    },
    handleStepExit() {
      this.currStep = null;
    }
  }
};
</script>

