<template>
  <div class="page-container" :style="{ backgroundColor: backgroundColor }">
       <div class="title">
    <h5>Erotic Cover Art</h5>
</div>
    <div class="chapter-container" :class="{ 'hidden': scrolledPastChapterTriggerPoint }">
      <div class="chapter">{{ event.chapter }}</div>
      <div class="vertical-line"></div>
    </div>
    <div class="gallery" style="top: 250px;">
      <div class="gallery-panel" v-for="photo in photos" :key="photo.id">
        <img :src="photo.Github_permalink" class="photo-image" @click="showPhotoInfo(photo)">
      </div>
    </div>
    <div class="modal" v-if="activePhoto">
      <div class="modal-content">
        <h2 style="font-size: 28px;">{{ activePhoto.title }}</h2>
        <p style="font-size: 20px;">By: {{ activePhoto.author}}</p>
        <p style="font-size: 18px;">{{ activePhoto.description }}</p>
        <p>Published: {{ activePhoto.date}}, {{ activePhoto.publisher}}</p>
        <button @click="closePhotoInfo">X</button>
      </div>
    </div>
    <div class="progress-container">
      <div class="container">
        <div class="wrapper">
          <h1 class="animated fadeIn"> Least Eroitc </h1>
          <div class="progress">
            <div class="progress-bar animated slideInDown" role="progressbar" aria-valuenow="85" aria-valuemin="0" aria-valuemax="100" style="height:85%">
            </div>
          </div>
          <h1 class="animated fadeIn"> Most Erotic</h1>
        </div>
      </div>
    </div>
<div class=".scroll-indicator-container">
  <Scrollama
    :offset=".5"
    @step-enter="handleStepEnter"
    @step-exit="handleStepExit"
  >
    <div v-for="n in 1" :key="n" :data-step-no="n" :class="{ 'step': true, 'active': n == currStep }">
      Explore how Erotic cover art has changed throughout over the past decade. Click on each photo to see book details and find one that resontates with you. 
    </div>
  </Scrollama>
</div>
  </div>
</template>



<script>
import photos from '/Users/sarahbacharach/Documents/majorstudio2/thesis/sbthesis/public/Data/smutty.json';
import "intersection-observer";
import Scrollama from "vue-scrollama";

export default {
  name: 'SmuttyCoverArt',
  components: {
    Scrollama,
  },
  data() {
    return {
      photos,
      backgroundColor: '#E6E6E6',
      event: { chapter: '' },
      scrolledPastChapterTriggerPoint: false,
      activePhoto: null,
      currStep: null,
      progressBarAnimated: false, 
    };
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll);
    this.handleScroll();
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.handleScroll);
  },

  methods: {
    handleScroll() {
      const scrollTop = window.scrollY;
      const progressBarTriggerPoint = 5100; 

      if (scrollTop >= progressBarTriggerPoint && !this.progressBarAnimated) {
        this.animateProgressBar();
      }
    },
    animateProgressBar() {
      const progressBar = document.querySelector('.progress-bar');
      progressBar.classList.add('animate');
      this.progressBarAnimated = true; 
    },
    showPhotoInfo(photo) {
      this.activePhoto = photo;
      console.log("Active Photo:", this.activePhoto);
    },
    closePhotoInfo() {
      this.activePhoto = null;
    },
    handleStepEnter({ element }) {
      this.currStep = element.dataset.stepNo;
    },
    handleStepExit() {
      this.currStep = null;
    },
  },
};
</script>




