<template>
  <div class="page-container" :style="{ backgroundColor: backgroundColor }">
    <div class="chapter-container" :class="{ 'hidden': scrolledPastChapterTriggerPoint }">
      <div class="chapter">{{ event.chapter }}</div>
      <div class="vertical-line"></div>
    </div>
    <BookChapter3/>
  </div>
</template>

<script>
import BookChapter3 from './BookChapter3.vue';

export default {
  name: 'ChapterThree',
  components: {
    BookChapter3
  },
  data() {
    return {
      backgroundColor: '#C0D9DC',
      event: { chapter: 'Ch. 4: Celebrating the Female Gaze' },
      scrolledPastChapterTriggerPoint: false 
    };
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll);
    this.handleScroll(); 
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.handleScroll);
  },
methods: {
  handleScroll() {
    const scrollTop = window.scrollY;
    const colorTriggerPoint = 8400; 
    const chapterTriggerPoint = 9000; 

    console.log('Scroll Top:', scrollTop);

    console.log('Color Trigger Point:', colorTriggerPoint);

    console.log('Chapter Trigger Point:', chapterTriggerPoint);

    if (scrollTop >= colorTriggerPoint) {
      this.backgroundColor = '#C0D9DC';
    } else {
      this.backgroundColor = '#C1B6D8';
    }

    if (scrollTop >= chapterTriggerPoint) {
      this.scrolledPastChapterTriggerPoint = true; 
    } else {
      this.scrolledPastChapterTriggerPoint = false; 
    }
  },
}
};
</script>

