<template>
  <div class="page-container" :style="{ backgroundColor: backgroundColor }">
    <div class="chapter-container" :class="{ 'hidden': scrolledPastChapterTriggerPoint }">
      <div class="chapter">{{ event.chapter }}</div>
      <div class="vertical-line"></div>
    <div>
    <div class="text-container">
        <div class="text-content">
            <h3>A well-designed cover can make or break a book's success. Decreasing popularity of <span style="background-color: #c1aadd">Erotic Cover Art, characterized by characters in states of undress</span>, and the increasing prevalence of <span style="background-color:#CEC26B">Decorative Cover Art depicting approachable illustrations </span> and <span style="background-color: #fc9058">Diverse Cover Art showcasing varied skin tones and ethnicities</span>, reflects a broading of who and what we consider to be apart of romance.
            </h3>
        </div>
    </div>
</div>
    </div>
<div class="charts-container">
  <div class="chart-container" id="smutty-chart">
    <h3 style="font-size: 30px; color: #c1aadd; font-family: 'EB Garamond';">Erotic Cover Art</h3>
    <svg width="370" height="600"></svg>
  </div>
  <div class="chart-container" id="decorative-chart">
    <h3 style="font-size: 30px; color: #CEC26B; font-family: 'EB Garamond';">Decorative Cover Art</h3>
    <svg width="370" height="600"></svg>
  </div>
  <div class="chart-container" id="diverse-chart">
    <h3 style="font-size: 30px; color: #E26728; font-family: 'EB Garamond';">Diverse Cover Art</h3>
    <svg width="470" height="500"></svg>
  </div>
</div>
  </div>
</template>

<script>
import * as d3 from 'd3';

export default {
  name: 'introCoverArt',
  data() {
    return {
      backgroundColor: '#E6E6E6',
      event: { chapter: 'Ch. 2: Evolving Ever After' },
      scrolledPastChapterTriggerPoint: false,
      chartData: [
        { label: 'Smutty', data: [29.82, 36.13, 27.73, 27.5, 27.27, 17.59, 17.35, 16.67, 9.01, 10.17, 5.88, 6.72, 2.56] },
        { label: 'Decorative', data: [7.02, 2.52, 5.88, 6.67, 5.45, 9.26, 11.22, 17.5, 23.42, 45.76, 57.14, 61.34, 71.79] },
        { label: 'Diverse', data: [5.26, 6.72, 5.88, 4.17, 7.27, 5.56, 4.08, 5.0, 7.21, 20.34, 26.05, 38.66, 22.22] },
      ],
      chartWidth: 470,
      chartHeight: 350,
    };
  },
  mounted() {
    this.renderSmuttyChart();
    this.renderDecorativeChart();
    this.renderDiverseChart();
    window.addEventListener('scroll', this.handleScroll);
    this.handleScroll();
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.handleScroll);
  },
  methods: {
    handleScroll() {
    },
    renderSmuttyChart() {
      const data = this.chartData[0];
      const svg = d3.select('#smutty-chart svg');
      this.renderChart(data, svg);
    },
    renderDecorativeChart() {
      const data = this.chartData[1];
      const svg = d3.select('#decorative-chart svg');
      this.renderChart(data, svg);
    },
    renderDiverseChart() {
      const data = this.chartData[2];
      const svg = d3.select('#diverse-chart svg');
      this.renderChart(data, svg);
    },
    renderChart(data, svg) {
      const years = [2011, 2012, 2013, 2014, 2015, 2016, 2017, 2018, 2019, 2020, 2021, 2022, 2023];
      const margin = { top: 70, right: 20, bottom: 10, left: 40 };
      const width = this.chartWidth - margin.left - margin.right;
      const height = this.chartHeight - margin.top - margin.bottom;

    const x = d3.scaleBand()
      .domain(years)
      .range([0, width])
      .padding(0.1);

    const y = d3.scaleLinear()
      .domain([0, d3.max(data.data)])
      .range([height, 0]);

    const chartSvg = svg.append('g')
      .attr('transform', `translate(${margin.left}, ${margin.top})`);

    chartSvg.append('g')
      .attr('class', 'x-axis')
      .attr('transform', `translate(0, ${height})`)
      .call(d3.axisBottom(x))
      .selectAll('text')
      .attr('transform', 'translate(-10,0)rotate(-45)')
      .style('text-anchor', 'end');

    chartSvg.append('g')
      .attr('class', 'y-axis')
      .call(d3.axisLeft(y));

    const bars = chartSvg.selectAll('.bar')
        .data(data.data) 
        .enter().append('rect')
        .attr('class', 'bar')
        .attr('x', (d, i) => x(years[i]))
        .attr('y', d => y(d))
        .attr('width', x.bandwidth())
        .attr('height', d => height - y(d))
        .attr('fill', data.label === 'Smutty' ? '#c1aadd' : data.label === 'Decorative' ? '#CEC26B' : '#E26728')
        .attr('data-value', d => d); 
        
    const hoverText = chartSvg.append('text')
        .attr('class', 'hover-text')
        .style('visibility', 'hidden') 
        .attr('text-anchor', 'middle')
        .attr('dy', -10)
        .style('font-weight', 'bold');


 bars.on('mouseover', function() {
    const hoveredData = parseFloat(d3.select(this).attr('data-value')); 


    const xPos = parseFloat(d3.select(this).attr('x')) + x.bandwidth() / 2;
    const yPos = parseFloat(d3.select(this).attr('y')) - 5;

    if (!isNaN(hoveredData)) {
        
        hoverText.attr('x', xPos)
            .attr('y', yPos)
            .text(`${hoveredData.toFixed(2)}%`) 
            .style('visibility', 'visible'); 
    }

    d3.select(this)
        .attr('fill', data.label === 'Smutty' ? '#e9d9fc' : data.label === 'Decorative' ? '#f7eb94' : '#faa378');
})
.on('mouseout', function() {
    d3.select(this)
        .attr('fill', data.label === 'Smutty' ? '#c1aadd' : data.label === 'Decorative' ? '#CEC26B' : '#E26728'); 
    hoverText.style('visibility', 'hidden'); 
});
}
  },
};
</script>






