<template>
  <div class="page-container" :style="{ backgroundColor: backgroundColor }">
    <div class="text-container">
      <div class="text-content">
        <h3>
          Being primarily by women, for women, and about women. Romance's strength lies in centering women as both authors and protagonists. This has facilitated equality across the publishing industry -  <span style="background-color: #672933;">The only creative field where women outnumber men at all levels of staff and the executive suite. </span> In 2020, women authored, read, sold, and bought the majority of new books.   
        </h3>  

      </div>
    </div>
    <img :src="currentImage" :alt="currentImageAlt" class="dynamic-image">
    <div class="chapter-container" :class="{ 'hidden': scrolledPastChapterTriggerPoint }">
      <div class="chapter">{{ event.chapter }}</div>
      <div class="vertical-line"></div>
    </div>
    <div class="buttons-container">
      <button @click="switchImage('AllLevels.svg')">All Levels</button>
      <button @click="switchImage('Exec.svg')">Executives</button>
    </div>
    <h2 class="title">Gender Distribution In Publishing</h2>
  </div>
</template>

<script>
export default {
  name: 'PublishingChart',
  data() {
    return {
      backgroundColor: '#C0D9DC',
      event: { chapter: 'Ch. 4: Celebrating the Female Gaze' },
      scrolledPastChapterTriggerPoint: false,
      currentImage: require('@/assets/AllLevels.svg'), 
      currentImageAlt: 'All Levels', 
    };
  },
  methods: {
    switchImage(imageName) {
      if (imageName === 'AllLevels.svg') {
        this.currentImage = require('@/assets/AllLevels.svg');
        this.currentImageAlt = 'All Levels';
      } else if (imageName === 'Exec.svg') {
        this.currentImage = require('@/assets/Exec.svg');
        this.currentImageAlt = 'Exec';
      }
    }
  }
};
</script>

