<template>
  <div class="page-container" :style="{ backgroundColor: backgroundColor }">
       <div class="title">
    <h5>Decor Cover Art</h5>
</div>
    <div class="chapter-container" :class="{ 'hidden': scrolledPastChapterTriggerPoint }">
      <div class="chapter">{{ event.chapter }}</div>
    </div>
    <div class="gallery" style="top: 250px;">
      <div class="gallery-panel" v-for="photo in photos" :key="photo.id">
        <img :src="photo.Github_permalink" class="photo-image" @click="showPhotoInfo(photo)">
      </div>
    </div>
    <div class="modal" v-if="activePhoto">
      <div class="modal-content">
        <h2 style="font-size: 28px;">{{ activePhoto.title }}</h2>
        <p style="font-size: 20px;">By: {{ activePhoto.author}}</p>
        <p style="font-size: 18px;">{{ activePhoto.description }}</p>
        <p>Published: {{ activePhoto.date}}, {{ activePhoto.publisher}}</p>
        <button @click="closePhotoInfo">X</button>
      </div>
    </div>
  
<div class=".scroll-indicator-container">
  <Scrollama
    :offset=".5"
    @step-enter="handleStepEnter"
    @step-exit="handleStepExit"
  >
    <div v-for="n in 1" :key="n" :data-step-no="n" :class="{ 'step': true, 'active': n == currStep }">
      Explore the decorative cover art which has become the leading trend in recent covers. Click on each photo to see book details and find one that resontates with you. 
    </div>
  </Scrollama>
</div>
  </div>
</template>



<script>
import photos from '/Users/sarahbacharach/Documents/majorstudio2/thesis/sbthesis/public/Data/decorative.json';
import "intersection-observer";
import Scrollama from "vue-scrollama";

export default {
  name: 'DecorCoverArt',
  components: {
    Scrollama,
  },
  data() {
    return {
      photos,
      backgroundColor: '#E6E6E6',
      event: { chapter: '' },
      scrolledPastChapterTriggerPoint: false, 
      activePhoto: null, 
      currStep: null, 
    };
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll);
    this.handleScroll(); 
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.handleScroll);
  },

  methods: {
    handleScroll() {
      const scrollTop = window.scrollY;
      const chapterTriggerPoint = 5600; 

      if (scrollTop >= chapterTriggerPoint) {
        this.scrolledPastChapterTriggerPoint = true; 
      } else {
        this.scrolledPastChapterTriggerPoint = false; 
      }
    },
    showPhotoInfo(photo) {
      this.activePhoto = photo; 
      console.log("Active Photo:", this.activePhoto);
    },
    closePhotoInfo() {
      this.activePhoto = null; 
    },
    handleStepEnter({ element }) {
      this.currStep = element.dataset.stepNo;
    },
    handleStepExit() {
      this.currStep = null;
    },
  },
};
</script>




