<template>
  <div class="title-page" :style="{ backgroundColor: backgroundColor }">
    <div class="chapter-container" :class="{ 'hidden': scrolledPastTriggerPoint }">
      <div class="chapter">{{ event.chapter }}</div>
      <div class="vertical-line"></div>
    </div>
    <BookFlipAnimation />
  </div>
</template>

<script>
import BookFlipAnimation from './BookFlipAnimation.vue';

export default {
  name: 'TitlePage',
  components: {
    BookFlipAnimation
  },
  data() {
    return {
      backgroundColor: '#f3e7d8',
      event: { chapter: 'Scroll' },
      scrolledPastTriggerPoint: false 
    };
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll);
    this.handleScroll();
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.handleScroll);
  },
  methods: {
    handleScroll() {
      const scrollTop = window.scrollY;
      const triggerPoint = 650; 

      if (scrollTop >= triggerPoint) {
        this.backgroundColor = '#FCEFBE';
        this.scrolledPastTriggerPoint = true; 
      } else {
        this.backgroundColor = '#E05892';
        this.scrolledPastTriggerPoint = false; 
      }
    }
  }
};
</script>

