

<template>
  <div class="page-container" :style="{ backgroundColor: backgroundColor }">
    <div class="chapter-container" :class="{ 'hidden': scrolledPastChapterTriggerPoint }">
      <div class="chapter">{{ event.chapter }}</div>
      <div class="vertical-line"></div>
    </div>
    <div class="text-container">
        <div class="text-content">
  <h3> Network analysis on GoodReads top 100 Books reveals romance's exceptional capacity for subgenre formation. Despite reader stereotypes- often dismissed for only reading romance - <span style="background-color: #fadd77">romance's reach is unmatched </span> - reaching practically every corner of literature.
  <br>                      
  <div style="text-align: center;font-size: 16px;">
    Click on nodes to explore the links to discover related genres, and uncover the connections that shape this literary landscape.
  </div>
</h3>
  </div>
</div>
      <div class="network-graph-container" ref="networkGraphContainer"></div>
    </div>

</template>


<script>
import * as d3 from 'd3';

export default {
  name: 'NetworkGraph',
  data() {
    return {
      backgroundColor: '#FCEFBE',
      event: { chapter: 'Ch. 1: Beyond the Beach Read' },
      dataLoaded: false, 
    };
  },
  mounted() {
    this.observer = new IntersectionObserver(this.handleIntersection, {
      root: null,
      rootMargin: '0px',
      threshold: 0.0001
    });

    this.observer.observe(this.$refs.networkGraphContainer);
  },

  methods: {
    handleIntersection(entries) {
      entries.forEach(entry => {
        if (entry.isIntersecting && !this.dataLoaded) { 
          this.loadProcessedData();
          this.dataLoaded = true; 
        }
      });
    },
    loadProcessedData() {
      const processedFilePath = '/Data/processed_romance.json';
      d3.json(processedFilePath).then(data => {
        this.drawNetworkGraph(data);
      }).catch(error => {
        console.error('Error loading processed data:', error);
      });
    },
    drawNetworkGraph(data) {
  const width = 900;
  const height = 850;

  const svg = d3.select(this.$refs.networkGraphContainer)
    .append('svg')
    .attr('width', width)
    .attr('height', height)

const simulation = d3.forceSimulation(data.nodes)
    .force('link', d3.forceLink(data.links).id(d => d.id).distance(linkDistance))
    .force('charge', d3.forceManyBody().strength(-450))
    .force('center', d3.forceX(width / 1.9)) 
    .force('verticalCenter', d3.forceY(height / 3).strength(0.3));
/* eslint-disable no-unused-vars */
function linkDistance(link) {
    return 360; 
}

  const link = svg.append('g')
    .attr('stroke', '#999')
    .attr('stroke-opacity', 0.6)
    .selectAll('line')
    .data(data.links)
    .join('line')
    .attr('stroke-width', d => Math.sqrt(d.value))
    .attr('marker-end', 'url(#arrow)');

  const node = svg.append('g')
    .attr('stroke', '#fff')
    .attr('stroke-width', 1)
    .selectAll('circle')
    .data(data.nodes)
    .join('circle')
    .attr('r', 35)
    .attr('fill', '#6B1F2B')
    .call(drag(simulation));

  node.append('title')
    .text(d => d.id);

const labels = svg.append('g')
  .selectAll('text')
  .data(data.nodes)
  .join('text')
  .attr('fill', '#fff')
  .attr('font-size', 13)
  .attr('text-anchor', 'middle')
  .attr('class', 'node-label')


  .text(d => d.id);

labels.each(wrap);

function wrap(d) {
  const text = d3.select(this);
  const width = 15; 
  const words = text.text().split(/\s+/).reverse();
  let line = [];
  let lineNumber = 0;
  const lineHeight = .8;
  let tspan = text.text(null).append('tspan').attr('x', 0).attr('y', 0);
  while (words.length > 0) {
    let word = words.pop();
    line.push(word);
    tspan.text(line.join(' '));
    if (tspan.node().getComputedTextLength() > width) {
      line.pop();
      tspan.text(line.join(' '));
      line = [word];
      tspan = text.append('tspan').attr('x', 0).attr('y', -10).attr('dy', ++lineNumber * lineHeight + 'em').text(word);
    }
  }
}
   

simulation.on('tick', () => {
  link.attr('x1', d => d.source.x)
    .attr('y1', d => d.source.y)
    .attr('x2', d => d.target.x)
    .attr('y2', d => d.target.y);

  node.attr('cx', d => d.x)
    .attr('cy', d => d.y);

  labels.attr('transform', d => `translate(${d.x},${d.y})`);
});

  // Functions for dragging nodes
  function drag(simulation) {
    function dragstarted(event) {
      if (!event.active) simulation.alphaTarget(0.3).restart();
      event.subject.fx = event.subject.x;
      event.subject.fy = event.subject.y;
    }

    function dragged(event) {
      event.subject.fx = event.x;
      event.subject.fy = event.y;
    }

    function dragended(event) {
      if (!event.active) simulation.alphaTarget(0);
      event.subject.fx = null;
      event.subject.fy = null;
    }

    return d3.drag()
      .on('start', dragstarted)
      .on('drag', dragged)
      .on('end', dragended);
  }
}
  }
};
</script>

